<script>
import {getPicCodeApi, getSmsCodeApi, loginApi} from '@/api/login'

export default {
  name: "Login",
  created() {
    this.getPicCode()
  },
  data() {
    return {
      picUrl: '',
      picCode: '',
      picKey: '',
      totalSecond: 60, // 总秒数
      currentSecond: 60, // 当前秒数
      timerId: null,
      mobile: null,
      smsCode: null,
      // 从其他页面拦截到登录页，登录成功要跳转回对应页面，如果不登录页要返回原来页面
      fromUrl: this.$route.query.fromUrl,
      toUrl: this.$route.query.toUrl,
    }
  },
  methods: {
    async getPicCode() {
      const {data} = await getPicCodeApi()
      this.picUrl = data.base64
      this.picKey = data.key
      this.$toast('获取图形验证码成功')
    },
    async getSmsCode() {
      // 验证手机号和图形验证码格式
      if (!this.validate()) return

      // 只有定时器（id）不存在，才能开启定时器
      if (this.timerId) return

      // 验证码发送成功才去开定时器，验证码发送不成功，要用户等60秒，这是不对的，而且请求验证码的前提是定时器没有打开
      await getSmsCodeApi(this.picCode, this.picKey, this.mobile)
      this.$toast('验证码发送成功，请注意查收')

      // 立即减少一秒并更新视图
      this.currentSecond--

      this.timerId = setInterval(() => {
        this.currentSecond--
        if (this.currentSecond <= 0) {
          clearInterval(this.timerId)
          this.timerId = null
          this.currentSecond = this.totalSecond
        }
      }, 1000)

    },
    validate() {
      // 匹配手机号
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确的手机号')
        return false
      }

      // 匹配图形验证码
      if (!/^[A-Za-z0-9]{4}$/.test(this.picCode)) {
        this.$toast('请输入正确的图形验证码')
        return false
      }

      return true
    },
    async login() {
      // 验证手机号和图形验证码格式
      if (!this.validate()) return

      if (!/^\d{6}$/.test(this.smsCode)) return this.$toast('请输入正确的短信验证码')

      const res = await loginApi(this.smsCode, this.mobile)

      // 往本地存储存入token和userId
      this.$store.commit('user/SET_USER_INFO', res.data)

      this.$toast('登录成功')

      // 如果 toUrl 存在，那要跳转到 toUrl，否则就正常访问主页
      this.$router.replace(this.toUrl || '/')
    },
    goBack() {
      // 如果存在 fromUrl ，将当前页面直接替换为 fromUrl (适用于把登录页从历史记录干掉的情况)
      // 如果没有，返回上一个页面(适用于保留登录页在历史记录的情况)
      if (this.fromUrl) return this.$router.replace(this.fromUrl)

      this.$router.go(-1)
    }
  },
  destroyed() {
    // 登录组件销毁的时候删除定时器
    clearInterval(this.timerId)
  }
}
</script>

<template>
  <div class="login">

    <van-nav-bar title="会员登录" left-arrow @click-left="goBack"/>

    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="请输入手机号码" type="text">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img v-show="picUrl" @click="getPicCode" :src="picUrl" alt="">
        </div>
        <div class="form-item">
          <input v-model="smsCode" class="inp" placeholder="请输入短信验证码" type="text">
          <button @click="getSmsCode">
            {{ timerId === null ? '获取验证码' : currentSecond + '秒后重新发送' }}
          </button>
        </div>
      </div>

      <div @click="login" class="login-btn">登录</div>
    </div>

  </div>
</template>

<style scoped lang="less">
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;

    h3 {
      font-size: 26px;
      font-weight: normal;
    }

    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;

    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }

    img {
      width: 94px;
      height: 31px;
    }

    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg, #ecb53c, #ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
